<template>
    <div class="row">
    <!--right-->
    <div data-app class="col-12">                   
            <div class="form-group">
            <!--live search-->           
                <div style="display:flex;flex-direction:row;margin-left:1em">
                    <div>
                        <label style="font-size:90%;">Select Department</label>
                        <select v-model="dept" class="form-control" v-if="dept_ready" @change="new_list" style='width:20em'>
                            <option v-for="dep in dept_list ">{{ dep.department }}</option>
                        </select>
                         <div style="display:flex;flex-direction:row">
                            <button type="button" class="form-control" @click="newplan" style="margin-top:0.4em;width:40%;margin-right:2%">New</button>  
                            <!--<button type="button" class="form-control" style="margin-top:0.4em;width:32.5%">Import</button>-->
                            <select v-model="status" class="form-control" v-if="dept_ready" @change="new_list" style="margin-top:0.45em;width:60%;margin-left:2%">
                                <option v-for="s in stl.length " :value="stl[s-1]">{{ stl_tag[s-1] }}</option>
                            </select>
                        </div>                                                          
                    </div>
                    <div style="margin-left:auto;margin-right:1em">
                        <label style="font-size:90%;">Search Record</label>
                        <input type='text' style="width:40em" class="form-control" placeholder="Search Record" v-model="key" @keyup="search_list">
                        <div style="display:flex;flex-direction:row;margin-top:0.4em">
                            <label style="margin-top:0.2em;margin-right:0.4em;">From</label>
                            <input type="month" v-model="st" class="form-control" style="width:95%" @change="new_list"/>
                            <label style="margin-top:0.2em;margin-right:0.4em;margin-left:0.4em">To</label>
                            <input type="month" v-model="ed" class="form-control" style="width:95%" @change="new_list"/>
                        </div>   
                    </div>
                </div>
            </div>           
            <div data-app class="col-12">                   
                <div class="form-group">
                    <div v-if="list_ready" v-html="printlist">
                        {{ printlist }}
                    </div>
                    <input type="hidden" id='v_reimb_f' @click="view_form"/>
                    <input type="hidden" id='edit_reimb_record' @click="edit_reimb_record"/>
                    <input type="hidden" id='reject_reimb_record' @click="reject_reimb_record"/>
                    <input type="hidden" id='cancel_reimb_record' @click="cancel_reimb_record"/>
                </div>
            </div>
            <!--list-->
    </div>
</div>
</template>
<script>
export default {
    data(){
        return{
            list_ready:false,
            dept_list:[],
            dept:'',
            dept_ready:false,
            assetlist:[],
            st:'',
            ed:'',
            insert_fund_name:'',
            fund_current : 'Regular',
            fund_type:['Regular','Special'],
            printlist:'',
            key:'',
            user_check:'',
            status:'*',
            stl_tag:['Active','Pending','Approved','Rejected','Cancelled'],
            stl:['*','Pending','Approved','Rejected','Cancelled'],
        }
    },
async created(){   
    await axios.get('Procurement/shared_backend/check_user_setup.php')
        .then(
            (response)=>{
                this.user_check = response.data[0].signed;
                var perm = response.data[0];
                if(!(this.user_check=='set')){
                    this.$router.push('../user_setup');
                }
                if(perm.proc_level<1){
                    this.$alert("No permission").then(
                        ()=>{
                            this.$router.go(-1);
                        }
                    )
                }
            }
        )
    await axios.get('Procurement/department/backend/dept_member.php')
        .then(
            (response)=>{
                this.dept_list = response.data;
                this.dept = this.dept_list[0].department;
                this.dept_ready = true;
                console.log(this.dept_list)
            }
        );
    await axios.get('Procurement/shared_backend/default_period.php')
        .then(
            (response)=>{
                this.st = response.data.st;
                this.ed = response.data.ed;
            }
        );    
    var formData = new FormData();
    formData.append('dept',this.dept);
    formData.append('st',this.st);
    formData.append('ed',this.ed);
    formData.append('stat',this.status);
    await axios.post('Procurement/reimbursement/backend/reimb_full_print_list.php',formData)
        .then(
            (response)=>{
                this.printlist = response.data;
                this.list_ready = true;
            }
        )    
},
mounted(){
    window.addEventListener('focus', this.focus_load);
},
beforeDestroy() {
  window.removeEventListener('focus', this.focus_load);
},
methods:{
    async focus_load(){       
        this.list_ready = false;
        this.printlist='';
        var formData = new FormData();
            formData.append('dept',this.dept);
            formData.append('st',this.st);
            formData.append('ed',this.ed);
            formData.append('stat',this.status);
        //setTimeout(function(){           
            await axios.post('Procurement/reimbursement/backend/reimb_full_print_list_search.php',formData)
                .then(
                    (response)=>{
                        this.printlist = response.data;
                        this.list_ready = true;
                    }
                ) 
       // }, 100);
    },
    search_list(event){
    var formData = new FormData();
    formData.append('dept',this.dept);
    formData.append('st',this.st);
    formData.append('ed',this.ed);
    formData.append('key',this.key);
    formData.append('stat',this.status);
    axios.post('Procurement/reimbursement/backend/reimb_full_print_list_search.php',formData)
        .then(
            (response)=>{
                this.printlist = response.data;
            }
        )
    },
    new_list(event){
        var formData = new FormData();
        formData.append('dept',this.dept);
        formData.append('st',this.st);
        formData.append('ed',this.ed);
        formData.append('stat',this.status);
        axios.post('Procurement/reimbursement/backend/reimb_full_print_list_search.php',formData)
            .then(
                (response)=>{
                    this.printlist = response.data;
                }
            )   
    },
    export_dept(){
        var formData = new FormData();
        formData.append('export','export');
        formData.append('st',this.st);
        formData.append('ed',this.ed);
        axios.post('Procurement/budget/backend/export.php',formData);

    },
    view_form(){
        var tag = document.getElementById('tmp_id').value;
        document.getElementById('st_tmp').value = this.st;
        document.getElementById('ed_tmp').value = this.ed;
        document.getElementById('doc_type').value = 'Reimbursement';
        this.$router.push('../wrapper/form_view');
    },
    newplan(){
        this.$router.push('reimbursement_form');
    },
    edit_reimb_record(){
        document.getElementById('st_tmp').value=this.st;
        document.getElementById('ed_tmp').value=this.ed;
        this.$router.push('edit_reimb_record');
    },
    reject_reimb_record(){    
        var id = document.getElementById('tmp_id').value;
        var formData = new FormData();
        formData.append('dept',this.dept);
        formData.append('tag',id);
        formData.append('st',this.st);
        formData.append('ed',this.ed);
        if(confirm('Confirm Rejecting Record : '+id+'?')==true){
        axios.post('Procurement/reimbursement/backend/reject.php',formData)
        .then(
            (response)=>{
                if(response.data=="OK"){
                    this.$alert("Successfully Rejected Record :"+id);
                    location.reload();
                } else {
                    this.$alert(response.data);
                }
            }
        )
        }
    },
    cancel_reimb_record(){
        var id = document.getElementById('tmp_id').value;
        var formData = new FormData();
        formData.append('dept',this.dept);
        formData.append('tag',id);
        formData.append('st',this.st);
        formData.append('ed',this.ed);
        if(confirm('Confirm Cancel Record : '+id+'?')==true){
        axios.post('Procurement/reimbursement/backend/cancelreimb.php',formData)
        .then(
            (response)=>{
                if(response.data=="OK"){
                    this.$alert("Successfully Rejected Record :"+id);
                    location.reload();
                } else {
                    this.$alert(response.data);
                }
            }
        )
    }
    }
},
}
</script>



